'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Select } from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"

interface Subscription {
  id: string
  customerId: string
  customerName?: string
  customerAddress?: string
}

interface Customer {
  id: string
  name: string
  address: string
  phone: string
}

export default function NyInspeksjon() {
  const router = useRouter()
  const [isLoading, setIsLoading] = useState(false)
  const [subscriptions, setSubscriptions] = useState<Subscription[]>([])
  const [customers, setCustomers] = useState<Record<string, Customer>>({})
  const [selectedSubscription, setSelectedSubscription] = useState('')
  const [season, setSeason] = useState('')
  const [images, setImages] = useState<File[]>([])
  const [imageUrls, setImageUrls] = useState<string[]>([])

  useEffect(() => {
    // Hent abonnementer og kunder fra localStorage
    const savedSubscriptions = JSON.parse(localStorage.getItem('subscriptions') || '[]')
    const savedCustomers = JSON.parse(localStorage.getItem('customers') || '[]')
    
    // Lag et oppslagsverk for kunder
    const customerMap = savedCustomers.reduce((acc: Record<string, Customer>, customer: Customer) => {
      acc[customer.id] = customer
      return acc
    }, {})
    
    // Legg til kundeinformasjon i abonnementene
    const enrichedSubscriptions = savedSubscriptions.map((sub: Subscription) => ({
      ...sub,
      customerName: customerMap[sub.customerId]?.name,
      customerAddress: customerMap[sub.customerId]?.address
    }))
    
    setSubscriptions(enrichedSubscriptions)
    setCustomers(customerMap)
  }, [])

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const newImages = Array.from(e.target.files)
      setImages(prev => [...prev, ...newImages])
      
      // Lag forhåndsvisnings-URLer
      const newUrls = newImages.map(file => URL.createObjectURL(file))
      setImageUrls(prev => [...prev, ...newUrls])
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)
    try {
      const formData = new FormData(e.target as HTMLFormElement)
      const customer = customers[subscriptions.find(s => s.id === selectedSubscription)?.customerId || '']
      
      const inspection = {
        id: Date.now().toString(),
        subscriptionId: selectedSubscription,
        customerId: customer?.id,
        customerName: customer?.name,
        customerAddress: customer?.address,
        customerPhone: customer?.phone,
        date: formData.get('date'),
        season: season,
        notes: formData.get('notes'),
        roofCondition: formData.get('roofCondition'),
        maintenanceNeeded: formData.get('maintenanceNeeded') === 'on',
        status: 'planlagt',
        images: imageUrls, // I en ekte app ville vi laste opp bildene til en server
        createdAt: new Date().toISOString()
      }

      const existingInspections = JSON.parse(localStorage.getItem('inspections') || '[]')
      localStorage.setItem('inspections', JSON.stringify([...existingInspections, inspection]))

      // Simuler SMS-sending (dette ville være en faktisk SMS-tjeneste i produksjon)
      if (formData.get('sendSms') === 'on') {
        console.log(`SMS ville blitt sendt til ${customer?.phone}: Vi kommer for å inspisere taket ditt den ${formData.get('date')}`)
      }

      alert('Inspeksjon planlagt')
      router.push('/inspeksjoner')
    } catch (error) {
      alert('Kunne ikke planlegge inspeksjon')
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="max-w-2xl mx-auto">
      <h1 className="text-2xl font-bold mb-6">Planlegg takvedlikehold</h1>

      <form onSubmit={handleSubmit} className="space-y-6">
        <Card className="p-6">
          <div className="space-y-4">
            <h2 className="text-lg font-semibold">Velg kunde og sesong</h2>
            
            <div>
              <label className="block text-sm font-medium mb-1">Abonnement</label>
              <Select 
                name="subscriptionId"
                value={selectedSubscription}
                onChange={(e) => setSelectedSubscription(e.target.value)}
                required
              >
                <option value="">Velg abonnement...</option>
                {subscriptions.map((sub) => (
                  <option key={sub.id} value={sub.id}>
                    {sub.customerName} - {sub.customerAddress}
                  </option>
                ))}
              </Select>
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Sesong</label>
              <Select 
                name="season"
                value={season}
                onChange={(e) => setSeason(e.target.value)}
                required
              >
                <option value="">Velg sesong...</option>
                <option value="vår">Vår</option>
                <option value="høst">Høst</option>
              </Select>
            </div>
          </div>
        </Card>

        <Card className="p-6">
          <div className="space-y-4">
            <h2 className="text-lg font-semibold">Inspeksjonsdetaljer</h2>
            
            <div>
              <label className="block text-sm font-medium mb-1">Planlagt dato</label>
              <Input type="date" name="date" required />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Taktilstand</label>
              <Select name="roofCondition" required>
                <option value="">Velg tilstand...</option>
                <option value="god">God</option>
                <option value="normal">Normal</option>
                <option value="dårlig">Dårlig</option>
                <option value="kritisk">Kritisk</option>
              </Select>
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Notater</label>
              <Textarea 
                name="notes" 
                placeholder="Beskriv takets tilstand og nødvendige tiltak..."
              />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Last opp bilder</label>
              <Input 
                type="file" 
                accept="image/*" 
                multiple 
                onChange={handleImageUpload}
              />
              <div className="grid grid-cols-3 gap-2 mt-2">
                {imageUrls.map((url, index) => (
                  <img 
                    key={index} 
                    src={url} 
                    alt={`Bilde ${index + 1}`} 
                    className="w-full h-24 object-cover rounded"
                  />
                ))}
              </div>
            </div>

            <div className="flex items-center space-x-2">
              <input 
                type="checkbox" 
                name="maintenanceNeeded" 
                id="maintenanceNeeded"
                className="rounded border-gray-300"
              />
              <label htmlFor="maintenanceNeeded">
                Vedlikehold nødvendig
              </label>
            </div>

            <div className="flex items-center space-x-2">
              <input 
                type="checkbox" 
                name="sendSms" 
                id="sendSms"
                className="rounded border-gray-300"
              />
              <label htmlFor="sendSms">
                Send SMS-varsel til kunde
              </label>
            </div>
          </div>
        </Card>

        <div className="flex justify-end space-x-4">
          <Button
            type="button"
            variant="outline"
            onClick={() => router.back()}
          >
            Avbryt
          </Button>
          <Button
            type="submit"
            disabled={isLoading || !selectedSubscription || !season}
          >
            {isLoading ? "Planlegger..." : "Planlegg inspeksjon"}
          </Button>
        </div>
      </form>
    </div>
  )
} 